footer {
	box-shadow: 0 -4px 15px -5px rgba(155, 168, 173, .41);
	z-index: 1;
	position: relative;
	padding: 0!important;
	
	@media (max-width: 992px) {
		.container{
			max-width: none;
		}
	  }
	.float-right {
		float: right !important;
	}
	.divider.green, hr.green {
		display: block;
		width: 100%;
		height: 0;
		margin: 20px 0;
		border: 0;
		border-top: 2px solid #f4f3f1;
		
		border-top-width: 1px;
		border-top-color: #008952;
	}
}

footer h5 {
	font-size: 16px
}

footer * {
	letter-spacing: 0!important
}

footer .icon-chevron-down {
	width: 20px!important;
	height: 20px!important;
	background-image: url("data:image/png;base64,")
}

footer [class*=icon-] {
	display: inline-block;
	width: 14px;
	height: 14px;
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: contain
}

footer [class*=icon-]:before {
	display: none
}

footer [class*=icon-].i-20 {
	width: 20px!important;
	height: 20px!important
}

footer [class*=icon-].i-24 {
	width: 24px!important;
	height: 24px!important
}

footer [class*=icon-].i-48 {
	width: 48px!important;
	height: 48px!important
}

footer .icon-mail {
	background-image: url("data:image/png;base64,")
}

footer .icon-location {
	background-image: url("data:image/png;base64,")
}

footer .icon-line {
	background-image: url("data:image/png;base64,")
}

footer .btn {
	font-size: 18px;
	padding: 0 28px;
	display: inline-block;
	height: 48px;
	line-height: 44px;
	margin-top: 10px;
	margin-bottom: 10px;
	min-width: 320px;
	max-width: 100%;
	text-align: center;
	text-decoration: none;
	border: 2px solid transparent;
	white-space: nowrap;
	border-radius: 5px;
	cursor: pointer;
	box-sizing: border-box
}

footer .btn+.btn {
	margin-left: 10px
}

footer .btn i[class*=icon-] {
	vertical-align: middle
}

footer .btn.active,
footer .btn.active:focus,
footer .btn.active:hover,
footer .btn.is-open,
footer .btn.is-open:focus,
footer .btn.is-open:hover,
footer .btn:active {
	opacity: 1;
	box-shadow: 0 0 0 0 transparent!important;
	color: #027638!important;
	background-image: linear-gradient(307deg, #6bca50, #099e69)!important
}

footer .btn.disabled,
footer .btn[disabled] {
	cursor: default!important;
	background: #e1e4e9!important;
	color: #aeb6bd!important;
	box-shadow: none!important;
	border: 0!important
}

footer .btn.btn-gray {
	border: 1px solid #d7e4e4
}

footer .btn.btn-gray .text-green {
	color: #00a94f!important
}

footer .btn.btn-gray:focus,
footer .btn.btn-gray:hover {
	background: #f9f8f7
}

footer a,
footer a:link {
	color: #00a94f
}

footer .brand-link {
	backgrond: #00a94f;
	background-image: linear-gradient(-42deg, #63bc55 9%, #30a75b);
	padding: 10px 0;
	color: #fff;
	font-size: 14px;
	line-height: 24px
}

.has-toolbar footer .brand-link {
	padding-bottom: 80px
}

@media (min-width:992px) {
	.has-toolbar footer .brand-link {
		padding-bottom: 0
	}
}

footer .brand-link hr {
	margin: 10px 0
}

footer .brand-link .menu {
	width: auto;
	height: auto!important
}

footer .brand-link .menu a.item {
	color: #fff;
	padding: 0;
	display: inline-block;
	height: 24px;
	line-height: 24px;
	font-size: 14px
}

footer .brand-link .menu a.item:focus,
footer .brand-link .menu a.item:hover {
	background: none;
	opacity: .87
}

footer .brand-link .menu a.item:first-child {
	border-left: 0!important;
	padding-left: 0!important
}

footer .brand-link .menu.paded {
	display: inline-block
}

footer .brand-link .menu.paded>a.item {
	float: left;
	padding: 0 6px
}

footer .brand-link .menu.dividing>a.item {
	position: relative;
	padding: 0 18px
}

footer .brand-link .menu.dividing>a.item:after {
	content: "";
	position: absolute;
	width: 1px;
	height: 10px;
	display: block;
	top: 0;
	bottom: 0;
	right: 0;
	background: #fff;
	margin: auto
}

footer .brand-link .menu.dividing>a.item:last-child:after {
	display: none
}

footer .contact-info {
	padding-top: 45px;
	padding-bottom: 40px
}

footer .contact-info .item {
	padding-left: 0
}

footer .contact-info .btn.btn-block {
	text-align: left;
	height: auto;
	max-width: 280px;
	padding: 12px 25px;
	margin: 0 0 16px auto
}

footer .contact-info .btn.btn-block:active {
	background: none!important
}

footer .contact-info .btn.btn-block img {
	line-height: 0;
	margin-right: 20px
}

footer .contact-info .btn.btn-block>* {
	vertical-align: middle 
}

footer .contact-info .btn.btn-block .text {
	font-size: 16px
}

footer .contact-info .btn.btn-block [class*=icon] {
	margin-right: 4px
}

footer .contact-info .image {
	float: left;
	width: 60px;
	text-align: right
}

footer .contact-info .image [class^=icon-] {
	font-size: 56px
}

footer .contact-info .content {
	display: block;
	margin-left: 60px;
	padding: 0 20px
}

footer .contact-info .content h3 {
	font-size: 24px;
	font-weight: 700;
	margin-top: 0;
	margin-bottom: 12px
}

footer .contact-info .content h3 small {
	display: block;
	font-size: 16px;
	color: #585858
}

footer .contact-info .content h3~.description {
	font-size: 14px;
	height: 48px;
	color: #7d8a94;
	line-height: 24px
}

footer .contact-info .content .item {
	margin-bottom: 15px
}

footer .contact-info .content .item h5,
footer .contact-info .content .item h5+a {
	font-size: 16px;
	margin: 0
}

@media screen and (max-width:992px) {
	footer .list>.item {
		padding: 12px 24px;
		display: flex;
		flex-direction: column;
		background: #fff;
		box-shadow: 0 1px 0 0 rgba(215, 228, 228, .3)
	}
	footer .list>.item.accordion {
		padding: 0
	}
	footer .list>.item.accordion.is-open .accordion-header .action {
		transform: rotate(180deg);
		display: flex;
		align-items: center;
		align-content: center
	}
	footer .list>.item.accordion .accordion-header {
		padding: 12px 24px;
		display: flex;
		flex-direction: row
	}
	footer .list>.item.accordion .accordion-header .content {
		overflow: visible
	}
	footer .list>.item.accordion .accordion-body {
		padding: 12px 24px;
		background: #f9f8f7
	}
	footer .list>.item.accordion .accordion-body br {
		display: block
	}
	footer .social-link {
		display: inline-block;
		width: 100%;
		padding: 16px 20px;
		text-align: center
	}
	footer .social-link a.item {
		display: inline-block;
		padding: 0 28px
	}
	footer .brand-link small {
		font-size: 14px
	}
	footer .brand-link .menu>a.item {
		position: relative;
		padding: 0;
		font-size: 14px;
		margin-bottom: 4px
	}
	footer .brand-link .menu>a.item:after {
		content: "";
		height: 12px;
		width: 1px;
		position: relative;
		top: 2px;
		display: inline-block;
		margin: 2px 12px;
		background: #fff;
		opacity: .4
	}
	footer .brand-link .menu>a.item:last-child:after {
		display: none
	}
}
footer .btn{
    box-shadow:none;
}
footer .item, a.item, a.item:link{
    color:#000;
}